<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <van-cell :title="item.title" is-link :to="item.path" v-for="item in list"/>

  </div>
</template>

<script>
  export default {
    name: "detail",
    data(){
      return {
        title:'',
        titles: {
          'zcx':'中诚信',
          'zzzx':'中债资信'
        },
        list: [
          {title: '业务介绍', path: '/mall/detail/businessIntroduction'},
          {title: '评级报告', path: '/mall/detail/ratingReport'},
          {title: '研究报告', path: '/mall/detail/researchReport'},
          {title: '付费报告', path: '/mall/detail/payReport'},
          {title: '沟通交流', path: '/mall/detail/communicate'},
        ]
      }
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      }
    },
    created() {
      this.title = this.titles[this.$route.name];
    }
  }
</script>

<style scoped>

  .van-cell {
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    padding: 12px 15px;
  }

</style>



